import React from 'react';
import styles from './LoadingScreen.module.css'

function LoadingScreen() {
    return (
        <div className={styles.wrapper}>
            <div className={styles.background} >
                <img className={styles.tmdb} src="https://camo.githubusercontent."></img>
                <div className={styles.tmdb}>This product uses the TMDb API but is not endorsed or certified by TMDb or Tinder.</div>
            </div>
        </div>
    );
  }
  
  export default LoadingScreen;